/*** Home ***/
.Home {
    display: flex;
    flex-direction: column;
    margin: 50px auto;

    @include mobile {
        margin: 25px auto;
    }

    &_contact {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        gap: 20px;

        & img {
            height: 42px;
        }

        &_cta {
            font-size: 24px;
            font-style: italic;
            color: $color-main-text;
            font-weight: 500;

            @include mobile {
                font-size: 12px;
            }
        }
    }

    &_cardlist {
        display: flex;
        justify-content: flex-start;
        box-sizing: border-box;
        flex-wrap: wrap;
        width: 86%;
        height: auto;
        border-radius: 25px;
        padding: 56px 50px 43px;
        margin: 43px auto 0px;
        gap: 60px 50px;
        background-color: $color-home-cardlist;

        @include mobile {
            border-radius: 10px;
            padding: 0;
            margin: 22px auto 0px;
            gap: 20px;
            background-color: $color-white;
        }

        & a {
            @include mobile {
                padding: auto;
            }
        }
    }
}