/*** Header ***/
.Header {
    display: flex;
    justify-content: center;
    justify-content: space-between;
    align-items: center;
    width: 86%;
    height: 68px;
    margin-top: 45px;

    @include mobile {
        margin-top: 20px;
    }

    // & img {
    //     width: auto;
    //     height: 96px;

    //     @include mobile {
    //         height: 62px;
    //     }
    // }

    &_name {
        display: flex;
        font-style: italic;
        font-size: 24px;
        font-weight: 700;
        color: $color-secondary-text;
    }

    & nav {
        display: flex;
        align-items: center;
        gap: 20px;
        font-weight: 500;

        & a {
            font-size: 24px;
            color: $color-main-text;

            @include mobile {
                font-size: 12px;
            }
        }

        @include mobile {
            text-transform: uppercase;
        }

        & a:focus {
            text-decoration-line: underline;
        }
    }
}